@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
}
.app {
  width: 100vw;
  height: 100vh;
  background: url("../assets/background.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.search {
  position: fixed;
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 15px;
  backdrop-filter: blur(5px);
  width: 300px;
  text-align: center;
  padding: 15px 5px;
  transform: translateY(-60px);
  left: calc(50% - 300px / 2);
  transition: transform 0.2s ease-in;

  &__input{
    border: none;
    border-bottom: 1px solid rgba($color: #000000, $alpha: 0.3);
    background-color: transparent;
    width: 90%;
    font-size: 1.2rem;
    font-weight: 500;
    color: rgba($color: #000000, $alpha: 0.6)
  }
   &__input:focus{
    outline: none;
  }
  &__input::placeholder{
    color: rgba($color: #000000, $alpha: 0.4)
  }
}
.show-search{
  transform: translateY(10px);
}

$width-window: 500px;
.window {
  position: fixed;
  width: $width-window;
  height: 400px;
  background-color: rgba(255, 255, 255, 0.4);
  top: calc(50% - $width-window/2);
  left: calc(50% - $width-window/2);
  border-radius: 30px;
  backdrop-filter: blur(5px);
}
.window__header {
  font-size: 1.7rem;
  font-weight: 700;
  text-align: center;
  padding-top: 30px;
  color: ghostwhite;
  text-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
}
.window__content {
  font-size: 5rem;
  font-weight: 400;
  text-align: center;
  padding-top: 65px;
  color: ghostwhite;
  text-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
}
